
html, body, #app {
  height: 100%;
}
#app {
  background: #F8F6F2;
}
.g-user-head, .g-user-head-default {
  cursor: pointer;
  border-radius: 30px;
  background: #64b1ca;
  color: #fff;
  line-height: 40px;
  text-align: center;
}
.g-user-head-default {
  width: 40px;
  height: 40px;
  font-size: 14px;
}
.mt_30 {
  margin-top: 30px;
}
.mt_20 {
  margin-top: 20px;
}
.item {
  height: 70px;
  border-bottom: solid 1px #f2f1f7;
  cursor: pointer;
}
.item .icon-container {
    display: inline-block;
    line-height: 70px;
    width: 50px;
}
.item .icon-container .icon {
      color: #3c8cad;
      font-size: 25px;
}
.item .text-container {
    display: inline-block;
    position: relative;
    top: 5px;
}
.item .text-container .description {
      color: gray;
      height: 20px;
}
.item .text-container:hover {
      color: #da4f4a;
}
.item .delete-container {
    display: none;
    float: right;
    margin: 5px 3px;
}
.item .delete-container:hover {
      color: #da4f4a;
}
.item:hover .delete-container {
    display: inline-block;
}
.content-main-container {
  background: #fff;
  min-height: 900px;
  margin: 7px 14px;
  padding: 21px;
}
.content-main-container .main-title {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border-bottom: solid 1px #f8f6f2;
    padding: 0 15px 14px 2px;
    font-weight: 400;
    color: #777;
    font-size: 17px;
    line-height: 100%;
    display: inline-block;
}



