
.left-panel {
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.left-menu {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #92908d;
  height: 100%;
  position: relative;
}
.left-menu .left-menu-tab {
    background-color: #2e2d2b;
    border-right: solid 1px #1a2224;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 60px;
    z-index: 4;
}
.left-menu .left-menu-top {
    text-align: center;
}
.left-menu .left-menu-top .left-panel-top-wrap {
      text-align: center;
}
.left-menu .left-menu-top .left-panel-top-wrap .left-panel-top {
        font-size: 25px;
        color: #fff;
        padding: 15px 0 8px;
        cursor: pointer;
}
.left-menu .left-panel-tab-wrap {
    position: absolute;
    top: 56px;
    bottom: 113px;
    left: 0;
    right: 0;
}
.left-menu .left-panel-tab {
    padding: 10px 0 0 0;
}
.left-menu .left-panel-footer {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    text-align: center;
}
.left-menu .left-panel-footer .user-setting {
      margin: 0 auto 15px;
}
.left-menu .quick-add {
    width: 60px;
    height: 30px;
    display: block;
    padding: 6px;
    text-align: center;
    color: #92908d;
    background-color: #2e2d2b;
    border-left: solid 3px transparent;
    cursor: pointer;
    margin-bottom: 15px;
}
.left-menu .quick-add:hover {
      color: #fff;
}
.left-menu .user-setting {
    width: 40px;
    height: 40px;
}
.popbox {
  position: absolute;
  display: block;
  left: 60px;
  min-width: 252px;
  max-width: 400px;
  text-align: left;
  background-color: #fff;
  border: 1px solid #eeece8;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  color: #484744;
}
.popbox-menu {
  padding: 5px;
  margin-bottom: 0px;
}
li {
  list-style: none;
}
.popbox-li {
  padding: 8px 30px;
  cursor: pointer;
  border-radius: 5px;
}
.popbox-li:hover {
    background: #f1ede5;
}
.divider {
  border: 0;
  border-top: solid 1px #e8edf3;
  margin: 0;
  padding: 0;
  cursor: default;
  height: 1px;
}
.divider:hover {
    background: #fff;
}



